import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import CardContent from "@mui/material/CardContent";

const dispatchedEvents = [];

const SrsMuiCardContent = function (props, ref) {
  return (
    <CardContent {...props} theme={props.muiTheme}>
      <slot />
    </CardContent>
  );
};

const styles = `
  /* Custom styles for the MuiCardContent component */
`;

const props = [{
  name: "sx",
  defaultValue: {},
  type: "object"
}];

const slots = [
  {
    name: "default",
    allowed: [],
  },
];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiCardContent),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
